@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
* {
  box-sizing: border-box;
}

input {
  all: unset;
  -webkit-appearance: none;
          appearance: none;
  box-sizing: border-box !important ;
}

body {
  background-color: #000;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid #000;
  text-align: center;
  background-color: #fff;
  color: #000;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
}

/* Auth */
.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: #fff;
  margin-bottom: 10px;
  font-size: 12px;
  color: #000;
  box-sizing: border-box;
}

.authSubmit {
  text-align: center;
  background: #04aaff;
  color: #fff;
  margin-top: 10px;
  cursor: pointer;
}

.authSwitch {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  border-radius: 20px;
  border: none;
  padding: 10px 0;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: #fff;
  cursor: pointer;
}

/* factoryForm */
.factoryForm,
.factoryInput__container {
  display: flex;
  align-items: center;
  width: 100%;
}

.factoryForm {
  flex-direction: column;
}

.factoryInput__container {
  position: relative;
  margin-bottom: 20px;
}

.factoryInput__input {
  flex-grow: 1;
  height: 40px;
  padding: 0 20px;
  color: #fff;
  border: 1px solid #04aaff;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.factoryInput__arrow {
  position: absolute;
  right: 0;
  background-color: #04aaff;
  height: 40px;
  width: 40px;
  padding: 10px 0;
  text-align: center;
  border-radius: 20px;
  color: #fff;
  box-sizing: border-box;
  cursor: pointer;
}

.factoryInput__label {
  color: #04aaff;
  cursor: pointer;
}

.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}

/* nweet */
.nweet {
  margin-bottom: 20px;
  background-color: #fff;
  width: 100%;
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
}

.nweet h4{
  margin-bottom: 10px;
}

.nweet__actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.nweet__actions span {
  cursor: pointer;
}

.nweet__actions span:first-child {
  margin-right: 10px;
}

.formInput {
  width: 100%;
  text-align: center;
}

.nweetEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.cancelBtn {
  cursor: pointer;
  background-color: tomato;
}

.profileForm {
  border-bottom: 1px solid hsla(0, 0%, 100%, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.logOut {
  margin-top: 50px;
  margin-bottom: 50px;
}

.factoryForm__attachment img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
  border: 1px solid rgb(255 255 255 / 50%);
}

.factoryForm__clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

